.teaml-reports-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  font-size: 12px;
  .teaml-reports-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 580px;
    .custom-width {
      width: 150px;
    }
    .custom-dropdown {
      width: 100px;
    }
    .button {
      padding: 10px 0px;
      width: 120px;
    }
  }
  .reports-table-container {
    position: relative;
    .bottom-border {
      div {
        padding: 12px 0px;
        border-bottom: 1px solid #999;
      }
    }
    .activity-formatter {
      line-height: 18px;
    }
  }

  .react-table-container {
    width: 100%;
    overflow-x: scroll;
    .ReactTable {
      .rt-table {
        .rt-tbody {
          .rt-tr-group {
            .rt-tr {
              .rt-td:last-child {
                // width: 250px !important;
                .audio-player {
                  width: 280px;
                  height: 35px;
                }
              }
            }
          }
        }
      }
    }
  }
}
